	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>🌡28℃ ☀</strong></p>\n<p>今日は一日中一人で毛呂山町内の公園のマッピングをやっていました。\n自宅で地図を書き写して、外に出て公園を探して、一人で遊びました。</p>\n<p>毛呂山町と一部西坂戸エリアを含め、七つの公園を開拓でき、遊び場が拡がりました。\n特にその中で見つけたばね仕掛けのシーソーと回転ジャングルジムは、一人でも飽きることなく遊べて楽しかったです。</p>","frontmatter":{"cover":null,"date":"1990-09-24T21:00:00+09:00","strDate":"1990/9/24","title":"日記"}}},"pathContext":{"path":"/ja/blog/1990/09/24-diary/","langKey":"ja"}}

